<template>
<!-- icon: 左边文字的icon -->
<!-- leftText: 左边文字 -->
<!-- rightColor: 右边文字的颜色 -->
<!-- rightText: 右边文字 -->
    <IRow type="flex" align="middle" class="row">
        <ICol span=6 offset=1>
        <IRow class="left">
            <ICol span=16 offset=2>
                <span class="leftText">{{leftText}}</span>
            </ICol>
        </IRow>
        </ICol>
        <ICol span=16 class="colRight">
        <span class="rightText" 
            :style="{color:rightColor}">{{rightText}}</span>
        </ICol>
    </IRow>
</template>
<script>
import { Row, Col } from 'iview/src/components/grid'
import Icon from 'iview/src/components/icon'
export default {
    name: 'GeneralList',
    components: {
        IRow: Row,
        ICol: Col,
        Icon
    },
    props: ['icon', 'leftText', 'rightColor', 'rightText']
}
</script>
<style lang="less" scoped>
.row {
    height: 45px;
    border-bottom: 1px solid #f8f8f8;
}

.left {
    display: flex;
    align-items: center;
}
.left-icon {
    text-align:center
}
.leftText {
    font-size: 15px;
    font-weight: bolder;
}

.colRight {
    text-align: right;
}

.rightText {
    font-size: 15px;
}
</style>



